import { Vue, Prop, Watch } from 'vue-property-decorator';
import moment from 'moment';
import { cloneDeep } from 'lodash';
import { arrayFind, currencyFormatter } from '@utils/util';
import { getSessionStorage } from '@utils/storage';
import {
  queryEditMarks,
  attachmentDownload,
  fetchBatchFlowVariable,
  fetchMeteorShower,
  tableDownload,
  tableUpload
} from '@api/processAssignment';
import { dataViewQueryWithAuth, fetchPushService } from '@api/viewData';
export default class PreviewFormMixin extends Vue {
  @Prop({ default: {} }) formAttribute: any;
  @Prop({ default: 'task' }) mode: string;
  @Prop({ default: '' }) taskId: string;
  @Prop({ default: '' }) processInstanceId: string;
  @Prop({ default: '' }) taskDealStationId: string;
  public formAttributeAlert: any = {};
  public userCode = '';
  public fileList: Array<any> = [];
  public modifysVisible = false;
  public dialogSwitch = false;
  public dialogTitle = '';
  public labelTable = '';
  public modifyHistoryList: Array<any> = [];
  public modifyColumns: Array<any> = [];
  public addTableList: Array<any> = [];
  public formConfig: any = {};
  public tableCompareContainer: Array<any> = [];
  public tableCompareColumns: Array<any> = [];
  public historyLoading = false;
  public editMarks: any = {};
  public nameScript = '';
  public scriptList: any = {};
  public uploadHeader: any = {
    Authorization: getSessionStorage('auth_token')
  };
  public childFromExist = false;
  public importFiles: Array<any> = [];
  public importDialogVisible = false;
  public loading = false;
  public uploadTableKey = '';
  public lastCall: any = null;
  public commonHistoryColumns: Array<any> = [
    {
      prop: 'taskName',
      label: '',
      'header-align': 'left',
      align: 'left'
    },
    {
      prop: 'value',
      label: '',
      'header-align': 'left',
      align: 'left'
    },
    {
      prop: 'handler',
      label: '',
      'header-align': 'left',
      align: 'left'
    },
    {
      prop: 'taskTime',
      label: '',
      'header-align': 'left',
      align: 'left'
    }
  ];

  @Watch('formAttribute', { deep: true, immediate: true })
  getVisible(newVal: any) {
    // 
    let ok = '';
    try {
      ok = JSON.stringify(this.formConfig);
    } catch (e) {
      console.log(e);
    }

    if (JSON.stringify(newVal) !== ok) {
      if (newVal && newVal.formList) {
        if (this.mode !== 'preview') {
          this.renderView(newVal);
        } else {
          this.refreshView(newVal);
        }
      }
    }
  }
  refreshView(formHandle) {
    this.formConfig = formHandle;
  }

  renderView(formHandle) {
    this.formConfig = { ...formHandle };
    // 
    this.formConfig.formList.forEach((item) => {
      if (item.editable === 1 || item.readable === 1) {
        //  editMarks 
        if (item.type !== 'table') {
          this.registerChangeListeners(item);
          // 
          if (item.filterText && item.filterText.length > 0) {
            item.filterText.forEach((ft) =>
              this.addFormLinkFields(item, ft.textLinkValue)
            );
          }
        } else {
          // 
          this.loadTableCols(item);
        }
        // 
        this.defaultValueBind(item);

        // 
        this.loadShowConfig(item);
      }
    });

    // 
    this.formConfig.formList.forEach((item) => {
      if (item.editable === 1 || item.readable === 1) {
        if (
          item.type === 'select' ||
          item.type === 'checkboxGroup' ||
          item.type === 'radioGroup' ||
          item.type === 'transfer'
        ) {
          this.$set(item, 'optionsList', []);
          this.$set(item, 'optionsType', '');
          if (item.sourceDataType === 'dataView') {
            // 
            setTimeout(() => {
              if (item.dataQueryList.length > 0) {
                this.listQueryHandle(
                  item,
                  item.dataQueryList,
                  item.dataSetVal,
                  'selectView'
                );
              } else {
                this.dataViewQueryHandle(item.dataSetVal, item, 'selectView');
              }
            }, 500);
          } else if (item.sourceDataType === 'variable') {
            if (item.flowVisibleCode) {
              this.getFlowVariablehttp('variableChange', item);
            }
          } else if (item.sourceDataType === 'static') {
            // 
            if (item.value) {
              setTimeout(() => {
                if (item.listeners && item.listeners.change) {
                  item.listeners.change(item.value, 'auto');
                }
              }, 500);
            }
          }
        } else if (item.type === 'table') {
          if (!item.refreshLoading && item.datas && item.datas.length > 0) {
            // 
            this.loadTableData(item, item.datas);
          } else {
            item.datas = [];
            if (
              Object.prototype.hasOwnProperty.call(item, 'defaultValue') &&
              item.defaultValue.valueShow
            ) {
              this.scriptComputHandle(item, item.defaultValue, 'tableDefault');
            } else {
              if (item.mutexSetList && item.mutexSetList.length > 0) {
                // 
                this.mutexTableLoading(item);
              } else if (item.flowVisibleCode) {
                this.getFlowVariablehttp('table', item);
              } else {
                if (item.dataQueryList.length > 0) {
                  this.listQueryHandle(
                    item,
                    item.dataQueryList,
                    item.dataSetVal,
                    'table'
                  );
                } else {
                  this.dataViewQueryHandle(item.dataSetVal, item, 'table');
                }
              }
            }
          }
        }
      }
    });
  }

  debunceInput(rowIndex, colProp, val, itemKey) {
    if (this.lastCall) {
      clearTimeout(this.lastCall);
    }
    this.lastCall = setTimeout(() => {
      this.handleDoInput(rowIndex, colProp, val, itemKey, false);
    }, 500);
  }

  handleDoInput(rowIndex, colProp, val, itemKey, isPre) {
    const tableObj = this.editMarks[itemKey];
    const editColObj = { prop: colProp };
    if (isPre) {
      editColObj['preValue'] = val;
    } else {
      editColObj['value'] = val;
    }
    if (!tableObj) {
      // 1.
      this.$set(this.editMarks, itemKey, { [rowIndex]: [editColObj] });
    } else {
      const currentRowRecord = tableObj[rowIndex];
      // 2.
      if (currentRowRecord) {
        // 2.1  
        const hasEditColIndex = currentRowRecord.findIndex((item) => {
          return item.prop === colProp;
        });
        if (hasEditColIndex === -1) {
          currentRowRecord.push(editColObj);
        } else {
          // currentRowRecord.splice(hasEditColIndex, 1, editColObj);
          currentRowRecord[hasEditColIndex]['value'] = val;
        }
      } else {
        // 2.2
        this.$set(tableObj, rowIndex, [editColObj]);
      }
      // 
      if (!isPre) {
        this.handleTableColScript(rowIndex, colProp, itemKey);
      }
    }
  }

  handleTableColScript(rowIndex, colProp, itemKey) {
    const tableItem = arrayFind(this.formConfig.formList, 'key', itemKey);
    if (tableItem.scriptObservers) {
      tableItem.scriptObservers.forEach((observer) => {
        // 
        if (
          observer.handleType === 'staticDefaultValue' ||
          observer.handleType === 'hiddenDatas'
        ) {
          // 
          if (
            observer.extendObj2.dataItem.editMarkIndex === rowIndex &&
            observer.extendObj.value === colProp
          ) {
            this.scriptComputHandle(
              observer.item,
              observer.scriptConfig,
              observer.handleType,
              observer.extendObj,
              observer.extendObj2
            );
          }
        }

        // 
        if (observer.handleType === 'defaultValue') {
          // 
          this.scriptComputHandle(
            observer.item,
            observer.scriptConfig,
            observer.handleType,
            observer.extendObj,
            observer.extendObj2
          );
        }
      });
    }
  }

  handleTableDatasChange(item) {
    this.computTableColEditControl(item);
    // 
    this.staticColumnDefaultValue(item);
    // 
    if (item.listeners && item.scriptObservers) {
      item.scriptObservers.forEach((entity: any) => {
        if (
          entity.handleType !== 'staticDefaultValue' &&
          entity.handleType !== 'hiddenDatas'
        ) {
          entity.extendObj.resultValue = item.datas; // 
          this.scriptComputHandle(
            entity.item,
            entity.scriptConfig,
            entity.handleType,
            entity.extendObj,
            entity.extendObj2
          );
        }
      });
    }
  }

  loadTableCols(item) {
    if (item.addSetList && item.addSetList.length > 0) {
      item.addSetList.forEach((addSet) => {
        if (addSet.showValueConfig) {
          // cols
          if (!item.oriCols) {
            item.oriCols = item.cols;
          }
          if (addSet.showValueConfig.showType === 'form') {
            const linkFather = arrayFind(
              this.formConfig.formList,
              'key',
              addSet.showValueConfig.valueShow
            );
            if (linkFather) {
              if (!linkFather.expandObservers) {
                linkFather.expandObservers = [];
              }

              let insertFlag = true;
              for (let j = 0; j < linkFather.expandObservers.length; j++) {
                const so = linkFather.expandObservers[j];
                if (
                  so.childKey === item.key &&
                  so.extendColName === addSet.extendColName
                ) {
                  insertFlag = false;
                  break;
                }
              }
              if (insertFlag) {
                linkFather.expandObservers.push({
                  ...addSet,
                  childKey: item.key
                });
              }

              if (!linkFather.listeners) {
                this.registerChangeListeners(linkFather);
              }
              if (addSet.showValueConfig.bindField) {
                this.addFormLinkFields(
                  linkFather,
                  addSet.showValueConfig.bindField
                );
              }
              if (linkFather.value || linkFather.value === false) {
                this.computeColsShowHide(linkFather);
              }
            }
          } else if (addSet.showValueConfig.showType === 'variable') {
            this.doFlowVariableQuery(addSet.showValueConfig.valueShow).then(
              (data: any) => {
                if (data || data === '') {
                  let result = 'none';
                  if (
                    this.computVaild(
                      addSet.showValueConfig.symbolShow,
                      data ? (Array.isArray(data) ? data.join(',') : data) : '',
                      addSet.showValueConfig.inputValue,
                      addSet.showValueConfig
                    )
                  ) {
                    result = 'show';
                  }
                  addSet.showForm = result;
                  this.doExtendColsHidden(item, addSet);
                }
              }
            );
          } else if (addSet.showValueConfig.showType === 'script') {
            this.scriptComputHandle(
              item,
              addSet.showValueConfig,
              'tableCols',
              null,
              addSet
            );
          }
        }
      });
    }
  }

  computeColsShowHide(item) {
    item.expandObservers &&
      item.expandObservers.forEach((expandList: any) => {
        this.$set(
          expandList,
          'showForm',
          this.vaildFormShow(expandList.showValueConfig, item, item.value)
        );
        const linkList = arrayFind(
          this.formConfig.formList,
          'key',
          expandList.childKey
        );
        this.doExtendColsHidden(linkList, expandList);
      });
  }

  // 
  doExtendColsHidden(item, selectedItem) {
    const addItem = arrayFind(
      item.addSetList,
      'extendColName',
      selectedItem.extendColName
    );
    if (addItem) {
      const afterCols = cloneDeep(item.cols);
      addItem.colsAdd.forEach((addSetCols) => {
        const col = arrayFind(afterCols, 'fieldCode', addSetCols.fieldCode);
        if (col) {
          if (selectedItem.showForm === 'none') {
            col.hidden = true;
          } else {
            // 
            const oriCol = arrayFind(
              item.oriCols,
              'fieldCode',
              addSetCols.fieldCode
            );
            if (oriCol) {
              col.hidden = oriCol.hidden;
            }
          }
        }
      });

      item.cols = afterCols;
    }
  }

  loadShowConfig(item) {
    if (!item.showValueConfig && item.valueShow) {
      // itemshowValueConfig
      item.showValueConfig = {
        showType: item.showType,
        valueShow: item.valueShow,
        targetType: item.targetType,
        bindField: item.bindField,
        symbolShow: item.symbolShow,
        inputValue: item.inputValue,
        parameterList: item.parameterList
      };
    }
    if (item.showValueConfig) {
      const sc = item.showValueConfig;
      if (sc.showType === 'form') {
        const linkFather = arrayFind(
          this.formConfig.formList,
          'key',
          sc.valueShow
        );
        if (linkFather) {
          if (linkFather.value) {
            item.showForm = this.vaildFormShow(
              sc,
              linkFather,
              linkFather.value
            );
          }
          if (!linkFather.showObservers) {
            linkFather.showObservers = [];
          }
          if (!arrayFind(linkFather.showObservers, 'key', item.key)) {
            linkFather.showObservers.push(item);
          }
          if (!linkFather.listeners) {
            this.registerChangeListeners(linkFather);
          }
          // 
          if (sc.bindField) {
            this.addFormLinkFields(linkFather, sc.bindField);
          }
        }
      } else if (sc.showType === 'variable') {
        this.doFlowVariableQuery(sc.valueShow).then((data: any) => {
          if (data || data === '') {
            let result = 'none';
            if (
              this.computVaild(
                sc.symbolShow,
                data ? (Array.isArray(data) ? data.join(',') : data) : '',
                sc.inputValue,
                sc
              )
            ) {
              result = 'show';
            }
            this.$set(item, 'showForm', result);
          }
        });
      } else if (sc.showType === 'script') {
        this.scriptComputHandle(item, sc, 'showAndHidden');
      }
    }
  }

  addFormLinkFields(item, field) {
    if (!item.linkFields) {
      item.linkFields = new Set<string>();
    } else if (item.linkFields instanceof Array) {
      item.linkFields = new Set(item.linkFields);
    }
    item.linkFields.add(field);
  }

  // 
  registerChangeListeners(item) {
    this.$set(item, 'listeners', {
      change: (val, flag) => {
        if (flag !== 'auto' && item.type !== 'table') {
          this.$set(this.editMarks, item.key, val);
        }
        // 
        item.filterObservers &&
          item.filterObservers.forEach((dataLink: any) => {
            this.datasFilter(dataLink);
          });
        // 
        item.showObservers &&
          item.showObservers.forEach((dataLink: any) => {
            this.$set(
              dataLink,
              'showForm',
              this.vaildFormShow(dataLink.showValueConfig, item, val)
            );
          });
        // 
        item.queryObservers &&
          item.queryObservers.forEach((entity: any) => {
            entity.linkSetItem.resultValue = this.getFormLinkValue(
              item,
              entity.linkSetItem
            ); // 
            this.listQueryHandle(
              entity.item,
              entity.dataQueryList,
              entity.viewsId,
              entity.typeData,
              entity.extendObj
            );
          });
        // 
        this.emitDVEvent(item);

        // 
        item.scriptObservers &&
          item.scriptObservers.forEach((entity: any) => {
            entity.extendObj.resultValue = this.getFormLinkValue(
              item,
              entity.extendObj
            ); // 
            if (
              entity.handleType !== 'staticDefaultValue' &&
              entity.handleType !== 'hiddenDatas'
            ) {
              this.scriptComputHandle(
                entity.item,
                entity.scriptConfig,
                entity.handleType,
                entity.extendObj,
                entity.extendObj2
              );
            } else {
              this.doStaticColumnComput(
                entity.item,
                entity.extendObj2.def,
                entity.extendObj,
                entity.handleType,
                entity.extendObj2.headIndex
              );
            }
          });

        // 
        this.computeColsShowHide(item);

        // 
        item.internalObservers &&
          item.internalObservers.forEach((formItem: any) => {
            this.buttonChange(formItem);
          });
      }
    });
  }

  emitDVEvent(item) {
    item.dvObservers &&
      item.dvObservers.forEach((entity: any) => {
        if (entity.type === 'table') {
          entity.cols.forEach((col) => {
            let result = '';
            let tableType = '';
            if (col.fieldType === 0) {
              tableType = 'string';
            } else if (col.fieldType === 1) {
              tableType = 'number';
            } else if (col.fieldType === 2) {
              tableType = 'date';
            } else if (col.fieldType === 3) {
              tableType = 'boolean';
            }
            if (col.defaultList && col.defaultList.cfgType === 'bind') {
              if (col.defaultList.bindType === 'tableSta') {
                entity.datas.forEach((detail) => {
                  const defValue = entity.datas.some((sta: any) => {
                    return sta[col.prop] === '';
                  });
                  if (defValue === true) {
                    entity.datas.forEach((detail) => {
                      detail[col.prop] = this.computDefaultValue(
                        detail[col.defaultList.bindTarget],
                        tableType,
                        col.defaultList.bindExpress
                      );
                    });
                  }
                });
              } else {
                result = this.getFormLinkValue(item, col.defaultList);
                const defResult = this.computDefaultValue(
                  result,
                  tableType,
                  col.defaultList.bindExpress
                );
                const defValue = entity.datas.some((sta) => {
                  return sta[col.prop] === '';
                });
                if (defValue === true) {
                  entity.datas.forEach((detail) => {
                    detail[col.prop] = defResult;
                  });
                }
              }
            }
          });
        } else {
          const dvc = entity.defaultValue;
          const result = this.getFormLinkValue(item, dvc);
          entity.value = this.computDefaultValue(
            result,
            entity.type,
            dvc.bindExpress
          );

          // 
          if (Object.prototype.hasOwnProperty.call(entity, 'realValue')) {
            this.setFormatWapper(entity);
          }
        }
        // 
        if (
          (entity.value || item.entity === '') &&
          entity.listeners &&
          entity.listeners.change
        ) {
          entity.listeners.change(entity.value, 'auto');
        }
      });
  }

  /**
   * 
   * code
   * code{code: result}
   * 
   */
  doFlowVariableQuery(vars) {
    return new Promise((resolve, reject) => {
      let expresses = vars;
      if (!Array.isArray(vars)) {
        expresses = [vars];
      }
      fetchBatchFlowVariable({ taskId: this.taskId, expresses }).then(
        ({ data, error }) => {
          if (!error) {
            if (Array.isArray(vars)) {
              resolve(data);
            } else {
              let result = data[vars];
              if (!result || result.length === 0) {
                result = null;
              }
              resolve(result);
            }
          } else {
            this.$message.error(error.message);
          }
        }
      );
    });
  }

  getFlowVariablehttp(handleId, item) {
    this.doFlowVariableQuery(item.flowVisibleCode).then((data: any) => {
      if (data) {
        if (handleId === 'variableChange') {
          const itemListData: Array<any> = [];
          data.forEach((itemList: any) => {
            itemListData.push({
              value: itemList[item.dataValue],
              label: itemList[item.dataLabel]
            });
          });
          this.$set(item, 'optionsList', itemListData);
          this.$set(item, 'textListLabel', data);
          this.$set(item, 'optionsType', 'viewsData');
          if (item.value && item.listeners) {
            item.listeners.change(item.value);
          }
          // item.type === 'transfer'
          if (item.type === 'select' || item.type === 'transfer') {
            this.datasFilter(item);
          }
        } else if (handleId === 'table') {
          if (data.length > 0) {
            if (
              item.cols.some((col) => col.prop && col.prop.indexOf('.') > -1)
            ) {
              // 
              this.loadTableData(item, data);
            } else {
              this.loadTableData(item, this.washTableFlowVariable(data));
            }
          }
        }
      }
    });
  }

  mutexTableLoading(item) {
    // 
    const vars = [
      item.flowVisibleCode,
      ...item.mutexSetList.map((set) => set.addDataSetVal)
    ];
    this.doFlowVariableQuery(vars).then((data: any) => {
      if (data) {
        for (let i = 0; i < vars.length; i++) {
          let list = data[vars[i]];
          if (list && list.length > 0) {
            // 1
            if (vars[i] !== item.flowVisibleCode) {
              list = this.washTableFlowVariable(list);

              const mutexSet = arrayFind(
                item.mutexSetList,
                'addDataSetVal',
                vars[i]
              );
              if (
                mutexSet.addViewDataLists &&
                mutexSet.addViewDataLists.length > 0
              ) {
                list.forEach((row) => {
                  mutexSet.addViewDataLists.forEach((col) => {
                    row[col['prop']] = row[col['bindProp']];
                  });
                });
              }

              this.loadTableData(item, list);
            } else {
              if (
                item.cols.some((col) => col.prop && col.prop.indexOf('.') > -1)
              ) {
                // 
                this.loadTableData(item, list);
              } else {
                this.loadTableData(item, this.washTableFlowVariable(list));
              }
            }
            break;
          }
        }
      }
    });
  }

  // 
  washTableFlowVariable(datas) {
    // prop
    return datas.map((ori) => {
      const newd: any = {};
      Object.keys(ori).forEach((key) => {
        newd[key.substring(key.lastIndexOf('.') + 1)] = ori[key];
      });
      return newd;
    });
  }

  // 
  defaultValueBind(item) {
    // 
    if (item.defaultValue) {
      if (
        !item.value ||
        item.value.length === 0 ||
        item.defaultValue.ignoreCurrVal
      ) {
        const dvc = item.defaultValue;
        if (dvc.cfgType === 'script') {
          this.scriptComputHandle(item, dvc, 'defaultValue');
        } else {
          if (dvc.bindType === 'form') {
            const linkFather = arrayFind(
              this.formConfig.formList,
              'key',
              dvc.bindTarget
            );
            if (linkFather) {
              if (dvc.bindField) {
                this.addFormLinkFields(linkFather, dvc.bindField);
              }
              if (linkFather.value) {
                const result = this.getFormLinkValue(linkFather, dvc);
                item.value = this.computDefaultValue(
                  result,
                  item.type,
                  dvc.bindExpress
                );
                // 
                if (
                  (item.value || item.value === '') &&
                  item.listeners &&
                  item.listeners.change
                ) {
                  item.listeners.change(item.value, 'auto');
                }
              }
              if (!linkFather.dvObservers) {
                linkFather.dvObservers = [];
              }
              if (!arrayFind(linkFather.dvObservers, 'key', item.key)) {
                linkFather.dvObservers.push(item);
              }
              if (!linkFather.listeners) {
                this.registerChangeListeners(linkFather);
              }
            }
          } else if (dvc.bindType === 'variable') {
            this.doFlowVariableQuery(dvc.bindTarget).then((data: any) => {
              if (data) {
                item.value = this.computDefaultValue(
                  Array.isArray(data) ? data.join(',') : data,
                  item.type,
                  dvc.bindExpress
                );
                if (
                  (item.value || item.value === '') &&
                  item.listeners &&
                  item.listeners.change
                ) {
                  item.listeners.change(item.value, 'auto');
                }
                // 
                if (Object.prototype.hasOwnProperty.call(item, 'realValue')) {
                  this.setFormatWapper(item);
                }
              }
            });
          } else if (dvc.bindType === 'static') {
            if (dvc.bindTarget) {
              if (item.type === 'date' && !dvc.bindTarget) {
                // 
                dvc.bindTarget = moment().format('YYYY-MM-DD');
              }
              item.value = this.computDefaultValue(
                dvc.bindTarget,
                item.type,
                dvc.bindExpress
              );
              if (
                (item.value || item.value === '') &&
                item.listeners &&
                item.listeners.change
              ) {
                item.listeners.change(item.value);
              }
              // 
              if (Object.prototype.hasOwnProperty.call(item, 'realValue')) {
                this.setFormatWapper(item);
              }
            }
          } else if (dvc.bindType === 'dataViews') {
            this.buttonChange(item);
          }
        }
      }
    } else {
      if (item.type === 'childFrom') {
        // 
        this.childFromChange(item);
      }
    }

    // 
    if (item.type === 'number') {
      this.setFormatWapper(item);

      this.$set(item, 'listeners', {
        ...item.listeners,
        focus: () => {
          this.$set(item, 'type', 'number');
          this.$set(item, 'value', item.realValue);
        },
        blur: () => {
          this.setFormatWapper(item);
        }
      });
    }
  }

  setFormatWapper(item) {
    this.$set(item, 'realValue', item.value);
    this.$set(item, 'type', 'text');
    let accuracyNum: any;
    if (item.accuracy || item.accuracy === 0) {
      accuracyNum = Number(item.accuracy);
    } else {
      accuracyNum = 2;
    }
    this.$set(item, 'value', currencyFormatter(item.value, accuracyNum));
  }

  // 
  childFromChange(item) {
    let childChange = false;
    item.value &&
      item.value.formList &&
      item.value.formList.some((val) => {
        if (val.value && val.value.length > 0) {
          childChange = true;
        }
      });
    if (childChange === false) {
      this.buttonChange(item);
    }
  }

  registerFormScriptParam(
    item,
    scriptConfig,
    handleType,
    extendObj2,
    linkSetItem
  ) {
    let result: any = null;
    /**
     * 
     * 1
     * 2
     * 3linkSetItem.resultValue
     */
    if (!linkSetItem.resultValue || linkSetItem.resultValue.length === 0) {
      const linkFather = arrayFind(
        this.formConfig.formList,
        'key',
        linkSetItem.value
      );
      if (linkFather) {
        if (!linkFather.scriptObservers) {
          linkFather.scriptObservers = [];
        }
        // 
        let insertFlag = true;
        if (
          handleType === 'paramsButtonValue' ||
          handleType === 'resultButtonValue'
        ) {
          insertFlag = false;
        } else {
          for (let j = 0; j < linkFather.scriptObservers.length; j++) {
            const so = linkFather.scriptObservers[j];
            if (so.item.key === item.key && so.handleType === handleType) {
              if (extendObj2) {
                if (handleType === 'tableCols') {
                  if (
                    so.extendObj2.extendColName === extendObj2.extendColName
                  ) {
                    insertFlag = false;
                  }
                } else if (
                  handleType === 'staticDefaultValue' ||
                  handleType === 'hiddenDatas'
                ) {
                  if (
                    so.extendObj2.def.fieldCode === extendObj2.def.fieldCode
                  ) {
                    insertFlag = false;
                  }
                }
              } else {
                insertFlag = false;
              }
              break;
            }
          }
        }

        insertFlag &&
          linkFather.scriptObservers.push({
            item,
            scriptConfig,
            handleType,
            extendObj: linkSetItem,
            extendObj2
          });

        if (!linkFather.listeners) {
          this.registerChangeListeners(linkFather);
        }
        // 
        if (linkSetItem.bindField) {
          this.addFormLinkFields(linkFather, linkSetItem.bindField);
        }
        // 1
        if (linkFather.type === 'table' && linkFather.datas.length > 0) {
          // 
          result = linkFather.datas;
        } else if (linkFather.value) {
          result = this.getFormLinkValue(linkFather, linkSetItem);
        }
      }
      if (result) {
        linkSetItem.resultValue = result;
      }
    } else {
      result = linkSetItem.resultValue;
    }
    return result;
  }

  /**
   * 
   * @param item
   * @param dataQueryList 
   * @param viewsIdid
   * @param typeData
   * @param extendObj
   */
  listQueryHandle(
    item,
    dataQueryList,
    viewsId,
    typeData,
    extendObj: any = null
  ) {
    if (dataQueryList.length > 0) {
      /**
       * 
       * 
       */
      Promise.all(
        dataQueryList.map((linkSetItem) => {
          return new Promise((resove, reject) => {
            const param = {};
            param['type'] = linkSetItem.type;
            param['code'] = linkSetItem.code;
            param['required'] = linkSetItem.required;

            if (linkSetItem.valueType === 'fixed') {
              // 
              param['value'] = linkSetItem.value;
              resove(param);
            } else if (linkSetItem.valueType === 'form') {
              // 
              /**
               * 
               * 1
               * 2
               * 3linkSetItem.relationVal
               */
              if (linkSetItem.resultValue) {
                param['value'] = linkSetItem.resultValue;
                resove(param);
              } else {
                const linkFather = arrayFind(
                  this.formConfig.formList,
                  'key',
                  linkSetItem.value
                );
                if (linkFather) {
                  // 2
                  if (!linkFather.queryObservers) {
                    linkFather.queryObservers = [];
                  }
                  linkFather.queryObservers.push({
                    item,
                    dataQueryList,
                    viewsId,
                    typeData,
                    extendObj,
                    linkSetItem
                  });
                  if (!linkFather.listeners) {
                    this.registerChangeListeners(linkFather);
                  }

                  if (linkFather.value) {
                    param['value'] = this.getFormLinkValue(
                      linkFather,
                      linkSetItem
                    );
                    resove(param);
                  } else {
                    reject(); // 
                  }
                }
              }
            } else if (linkSetItem.valueType === 'variable') {
              // 
              if (linkSetItem.relationVal) {
                // 
                param['value'] = linkSetItem.relationVal;
                resove(param);
              } else {
                this.doFlowVariableQuery(linkSetItem.value).then(
                  (data: any) => {
                    if (data) {
                      linkSetItem.relationVal = Array.isArray(data)
                        ? data.join(',')
                        : data;
                      param['value'] = linkSetItem.relationVal;
                      resove(param);
                    } else {
                      reject();
                    }
                  }
                );
              }
            } else {
              // 
              resove({});
            }
          });
        })
      )
        .then((paramsValues) => {
          // 
          if (
            paramsValues.every((pv: any) => {
              let re = true;
              if (pv.required === 0 && (!pv.value || pv.value.length === 0)) {
                re = false;
              }
              return re;
            })
          ) {
            // 
            this.dataViewQueryHandle(
              viewsId,
              item,
              typeData,
              paramsValues.filter((pa: any) => pa['code']),
              extendObj
            );
          }
        })
        .catch(() => {});
    }
  }

  /**
   * 
   * @param item: 
   * @param scriptConfig: 
   * @param handleType: 
   * @param extendObj:  
   */
  scriptComputHandle(
    item,
    scriptConfig,
    handleType,
    extendObj: any = null,
    extendObj2: any = null
  ) {
    Promise.all(
      scriptConfig.parameterList.map((linkSetItem) => {
        return new Promise((resove, reject) => {
          const param = { label: linkSetItem.label };
          if (linkSetItem.valueType === 'fixed') {
            // 
            param['value'] = linkSetItem.value;
            resove(param);
          } else if (linkSetItem.valueType === 'form') {
            // 
            const result = this.registerFormScriptParam(
              item,
              scriptConfig,
              handleType,
              extendObj2,
              linkSetItem
            );
            // 
            param['value'] = result ? result : '';
            resove(param);
          } else if (linkSetItem.valueType === 'variable') {
            // 
            if (linkSetItem.resultValue) {
              param['value'] = linkSetItem.resultValue;
              resove(param);
            } else {
              this.doFlowVariableQuery(linkSetItem.value).then((data: any) => {
                if (data) {
                  linkSetItem.resultValue =
                    Array.isArray(data) && typeof data[0] !== 'object'
                      ? data.join(',')
                      : data;
                  param['value'] = linkSetItem.resultValue;
                  resove(param);
                } else {
                  // 
                  linkSetItem.resultValue = '';
                  param['value'] = '';
                  resove(param);
                }
              });
            }
          } else if (linkSetItem.valueType === 'tableSta') {
            // 
            let unStaticFlag = false;
            // 
            if (handleType === 'paramsButtonValue') {
              param['value'] = extendObj2['row'][linkSetItem.value];
            } else {
              const currentRow = arrayFind(
                item.datas,
                'editMarkIndex',
                extendObj2.dataItem['editMarkIndex']
              );
              if (currentRow) {
                const resultValue: any = currentRow[linkSetItem.value];
                if (resultValue) {
                  param['value'] = resultValue;
                }
              }

              if (
                !extendObj2['scriptFlag'] ||
                !extendObj2['scriptFlag'][`${handleType}_${linkSetItem.value}`]
              ) {
                // 
                if (!item.scriptObservers) {
                  item.scriptObservers = [];
                }
                item.scriptObservers.push({
                  item,
                  scriptConfig,
                  handleType,
                  extendObj: linkSetItem,
                  extendObj2
                });
                if (!item.listeners) {
                  this.registerChangeListeners(item);
                }
                if (!extendObj2['scriptFlag']) {
                  extendObj2['scriptFlag'] = {};
                }
                extendObj2['scriptFlag'][
                  `${handleType}_${linkSetItem.value}`
                ] = true;

                if (extendObj2.def.type !== 'static') {
                  unStaticFlag = true;
                }
              }
            }

            if (!unStaticFlag || extendObj2.ignoreFlag) {
              resove(param);
            } else {
              reject();
            }
          } else if (linkSetItem.valueType === 'serviceResponse') {
            if (linkSetItem.value) {
              const serList = scriptConfig.returnData.map((reItem) => {
                return reItem[linkSetItem.value];
              });
              param['value'] = Array.isArray(serList)
                ? serList.join(',')
                : serList;
            } else {
              // 
              param['value'] = scriptConfig.returnData;
            }

            if (param['value']) {
              resove(param);
            } else {
              reject();
            }
          }
        });
      })
    )
      .then((paramsValues) => {
        // 
        new Promise((resolve, reject) => {
          if (
            scriptConfig.scriptType === 'javascript' ||
            !scriptConfig.scriptType
          ) {
            // 
            const paramsList = paramsValues.map((par: any) => {
              return par.value;
            });

            if (scriptConfig.valueShow) {
              const methodChage = eval(
                `window.dealFormScript = ${scriptConfig.valueShow}`
              );

              if (handleType === 'tableDefault') {
                for (let i = 0; i < paramsList.length; i++) {
                  if (Array.isArray(paramsList[i])) {
                    paramsList[i] = this.washTableFlowVariable(paramsList[i]);
                  }
                }
              }
              resolve(methodChage(...paramsList));
            } else {
              reject();
            }
          } else if (scriptConfig.scriptType === 'meteorShower') {
            // 
            let meteorShower = scriptConfig.valueShow;
            // 
            paramsValues.forEach((par: any) => {
              meteorShower = meteorShower.replace(
                new RegExp(par['label'], 'g'),
                `"${par['value']}"`
              );
            });
            // 
            fetchMeteorShower({
              script: meteorShower,
              taskId: this.taskId
            }).then(({ data }: any) => {
              if (data) {
                resolve(Array.isArray(data) ? data.join(',') : data);
              } else {
                resolve('');
              }
            });
          }
        })
          .then((result: any) => {
            if (handleType === 'defaultValue') {
              // 
              item.value = result;
              if (item.listeners) {
                item.listeners.change(item.value);
              }
              // 
              if (Object.prototype.hasOwnProperty.call(item, 'realValue')) {
                this.setFormatWapper(item);
              }
            } else if (handleType === 'showAndHidden') {
              // 
              this.$set(item, 'showForm', 'none');
              if (result === true) {
                this.$set(item, 'showForm', 'show');
              }
            } else if (handleType === 'tableCols') {
              let showForm = 'show';
              if (!result || result === 'false') {
                showForm = 'none';
              }
              extendObj2.showForm = showForm;
              this.doExtendColsHidden(item, extendObj2);
            } else if (handleType === 'staticDefaultValue') {
              const newData: Array<any> = cloneDeep(
                arrayFind(
                  item.datas,
                  'editMarkIndex',
                  extendObj2.dataItem['editMarkIndex']
                )
              );
              if (newData) {
                newData[scriptConfig.fieldCode] = result;
                item.datas.splice(extendObj2.ind, 1, newData);
                // 
                const oriData: Array<any> = arrayFind(
                  item.oriDatas,
                  'editMarkIndex',
                  extendObj2.dataItem['editMarkIndex']
                );
                let doInput = result ? true : false;
                if (oriData) {
                  const oldV = oriData[scriptConfig.fieldCode];

                  oriData[scriptConfig.fieldCode] = result;

                  if (doInput && oldV == result) {
                    // 
                    doInput = false;
                  }
                }
                // 
                doInput &&
                  this.handleDoInput(
                    extendObj2.dataItem['editMarkIndex'],
                    scriptConfig.fieldCode,
                    result,
                    item.key,
                    false
                  );
              }
            } else if (handleType === 'hiddenDatas') {
              if (!result) {
                this.$set(extendObj.disable, extendObj2.headIndex, 0);
              } else {
                this.$set(extendObj.disable, extendObj2.headIndex, 1);
              }
            } else if (handleType === 'paramsButtonValue') {
              scriptConfig.reqBody = result;
              // 
              this.fetchBtnService(extendObj2.row, extendObj2.btn, item);
            } else if (handleType === 'ordinaryButtonParams') {
              scriptConfig.reqBody = result;
              // 
              this.fetchOrdinaryBtnService(
                extendObj2.linkPromise,
                item,
                extendObj2.linkDef
              );
            } else if (handleType === 'resultButtonValue') {
              this.$set(extendObj2, scriptConfig.targetCol, result);
            } else if (handleType === 'ordinaryButtonValue') {
              const linkFather = arrayFind(
                this.formConfig.formList,
                'key',
                item.setLink.targetObjectBut.targetCol
              );
              this.formConfig.formList.forEach((fromItem) => {
                if (fromItem.key === linkFather.key) {
                  fromItem.value = result;
                  if (item.setLink.targetObjectBut.targetTableCol) {
                    fromItem.datas.forEach((ele) => {
                      ele[item.setLink.targetObjectBut.targetTableCol] = result;
                    });
                  } else {
                    fromItem.value = result;
                  }
                }
              });
            } else if (handleType === 'defScriptValue') {
              item.value = result;
            } else if (handleType === 'tableDefault') {
              this.loadTableData(item, result);
            } else if (handleType === 'datasfilter') {
              if (item.type === 'table') {
                item.datas = result;
              } else {
                item.optionsList = result.map((option) => ({
                  value: option[item.dataValue],
                  label: option[item.dataLabel]
                }));
              }
            }
          })
          .catch(() => {});
      })
      .catch(() => {});
  }

  /**
   * 
   */
  getViewQueryFields(item, dataHandle, extendObj) {
    let fields: any = [];
    if (dataHandle === 'selectView') {
      if (!item.linkFields) {
        item.linkFields = new Set();
      } else {
        if (Array.isArray(item.linkFields)) {
          item.linkFields = new Set(item.linkFields);
        } else {
          item.linkFields = new Set();
        }
      }
      item.linkFields.add(item.dataValue);
      item.linkFields.add(item.dataLabel);

      fields = Array.from(item.linkFields);
    } else if (dataHandle === 'tableChild') {
      // 
      const fs = new Set(
        extendObj.addViewDataLists.map((av) => av.addViewData)
      );
      // 
      extendObj.addDataValue.forEach((fie) => fs.add(fie));
      fields = Array.from(fs);
    } else if (dataHandle === 'table') {
      // 
      const fs = new Set(item.associatedNodeList);
      // 
      if (item.addSetList && item.addSetList.length > 0) {
        item.addSetList.forEach((addSet) => {
          if (addSet.addSetType !== 'addList') {
            addSet.addViewDataLists.forEach(
              (av) =>
                av.addDataLink.indexOf('yss') > -1 && fs.add(av.addDataLink)
            );
          }
        });
      }
      fields = Array.from(fs);
    }
    return fields;
  }

  dataViewQueryHandle(
    viewsId,
    item,
    dataHandle,
    queryParams: any = [],
    extendObj: any = null
  ) {
    if (viewsId) {
      const viewData = {
        viewId: viewsId,
        queryParams: queryParams ? queryParams : [],
        authStationAndUsers: [
          {
            stationId: this.taskDealStationId,
            userCode: this.userCode
          }
        ]
      };
      dataViewQueryWithAuth(viewData).then(({ data, error }) => {
        if (error) {
          this.$message.error(':' + error.message);
          return false;
        } else {
          if (data.rows && data.rows.length > 0) {
            if (dataHandle === 'tableChild') {
              this.mergeExtendColsView(
                data.rows,
                extendObj.addViewDataLists,
                extendObj,
                item
              );
            } else if (dataHandle === 'selectView') {
              const itemListData: Array<any> = [];
              data.rows.forEach((itemList: any) => {
                if (Array.isArray(item.dataLabel)) {
                  const label: Array<any> = [];
                  item.dataLabel.forEach((ele, index) => {
                    label.push(itemList[ele]);
                  });
                  if (
                    !arrayFind(itemListData, 'value', itemList[item.dataValue])
                  ) {
                    itemListData.push({
                      value: itemList[item.dataValue],
                      label: label.join('-')
                    });
                  }
                } else {
                  itemListData.push({
                    value: itemList[item.dataValue],
                    label: itemList[item.dataLabel]
                  });
                }
              });
              // 
              if (Array.isArray(item.dataLabel) && item.dataLabel.length > 1) {
                itemListData.sort((pre, aft) => {
                  return pre['label'] > aft['label'] ? 1 : -1;
                });
              }
              this.$set(item, 'textListLabel', data.rows);
              this.$set(item, 'optionsList', itemListData);
              this.$set(item, 'optionsType', 'viewsData');
              if (item.value && item.listeners) {
                item.listeners.change(item.value, 'auto');
              }
              if (item.type === 'select' || item.type === 'transfer') {
                this.datasFilter(item);
              }
            } else if (dataHandle === 'table') {
              this.loadTableData(item, data.rows);
            }
          }
        }
      });
    }
  }

  // 
  loadTableData(item, records) {
    if (records && records.length > 0) {
      //   editMarkIndex
      records.forEach((record, index) => {
        let key: any = null;
        if (item.refreshLoading && item.loadingPrimKey.length > 0) {
          key = item.loadingPrimKey.map((key) => record[key]).join('_');
        } else if (!record['editMarkIndex']) {
          // editMarkIndex
          key = `r${index}`;
        }
        if (key) {
          this.$set(record, 'editMarkIndex', key);
        }
      });
      // 
      item.oriDatas = records;
      // 
      this.datasFilter(item);

      // 
      if (this.mode === 'historyDetail') {
        return false;
      }
      // 
      if (item.history) {
        this.queryItemImprint(item);
      }

      // 
      if (item.addSetList && item.addSetList.length > 0) {
        item.addSetList.forEach((addSet) => {
          this.doExtendColsQuery(addSet, item);
        });
      }
      if (item.value && item.listeners) {
        item.listeners.change(item.value);
      }
    }
  }

  doExtendColsQuery(addSet, item) {
    const records = item.oriDatas;
    if (addSet.addSetType === 'addList') {
      // 
      if (addSet.tabList.length > 0 || addSet.colsAdd.length > 0) {
        // 
        const cols =
          addSet.colsAdd.length > 0 ? addSet.colsAdd : addSet.tabList;
        this.recordsHanle(records, item, cols);
      }
      this.datasFilter(item);
    } else if (addSet.addSetType === 'addViewList') {
      // 
      if (addSet.addDataQueryList.length > 0) {
        this.listQueryHandle(
          item,
          addSet.addDataQueryList,
          addSet.addDataSetVal,
          'tableChild',
          addSet
        );
      } else {
        this.dataViewQueryHandle(
          addSet.addDataSetVal,
          item,
          'tableChild',
          [],
          addSet
        );
      }
    } else if (addSet.addSetType === 'addFlowVariables') {
      // 
      this.addFlowVariablesLink(addSet, addSet.addViewDataLists, item, records);
    }
  }

  // 
  getFormLinkValue(formItem, linkSetItem) {
    let result: any = '';
    if (linkSetItem.bindField) {
      if (formItem.textListLabel && formItem.textListLabel.length > 0) {
        const fobj = arrayFind(
          formItem.textListLabel,
          formItem.dataValue,
          formItem.value
        );
        if (fobj) {
          result = fobj[linkSetItem.bindField];
        }
      }
      this.addFormLinkFields(formItem, linkSetItem.bindField);
    } else {
      result = formItem.realValue ? formItem.realValue : formItem.value;
    }
    return result;
  }

  // registerFormScriptParam

  doStaticColumnComput(item, def, extendObj, handleType, headIndex) {
    item.datas.forEach((dataItem, index) => {
      if (handleType === 'hiddenDatas') {
        dataItem.disable = new Array(item.cols.length);
      }
      if (
        def.type === 'static' &&
        handleType === 'staticDefaultValue' &&
        dataItem[def.fieldCode]
      ) {
        // 
      } else {
        const dataList = {
          dataItem: dataItem,
          def: def,
          ind: index,
          headIndex,
          ignoreFlag: true
        };
        this.scriptComputHandle(
          item,
          def.defaultList,
          handleType,
          extendObj,
          dataList
        );
      }
    });
  }

  // 
  staticColumnDefaultValue(item) {
    let tableType: any = '';
    item.cols.forEach((def: any) => {
      if (def.defaultList) {
        if (def.fieldType === 0) {
          tableType = 'string';
        } else if (def.fieldType === 1) {
          tableType = 'number';
        } else if (def.fieldType === 2) {
          tableType = 'date';
        } else if (def.fieldType === 3) {
          tableType = 'boolean';
        }
        if (def.defaultList.cfgType === 'script') {
          if (def.type !== 'static' && def.editable !== 1) {
            // 
          } else {
            const vars: any = [];
            def.defaultList.parameterList.forEach((pl) => {
              if (!pl.resultValue) {
                if (pl.valueType === 'variable') {
                  vars.push(pl.value);
                } else if (pl.valueType === 'form') {
                  // item, scriptConfig, handleType, extendObj2, linkSetItem
                  pl.resultValue = this.registerFormScriptParam(
                    item,
                    def.defaultList,
                    'staticDefaultValue',
                    { def },
                    pl
                  );
                }
              }
            });

            new Promise((resolve) => {
              if (vars.length > 0) {
                this.doFlowVariableQuery(vars).then((data: any) => {
                  if (data) {
                    def.defaultList.parameterList.forEach((pl) => {
                      if (pl.valueType === 'variable' && !pl.resultValue) {
                        pl['resultValue'] = data[pl.value];
                      }
                    });
                  }
                  resolve();
                });
              } else {
                resolve();
              }
            }).then(() => {
              this.doStaticColumnComput(
                item,
                def,
                null,
                'staticDefaultValue',
                null
              );
            });
          }
        } else {
          if (def.defaultList.bindType === 'static') {
            if (def.fieldType === 2 && !def.defaultList.bindTarget) {
              // 
              def.defaultList.bindTarget = moment().format('YYYY-MM-DD');
            }
            if (def.defaultList.bindTarget) {
              const staticDef = this.computDefaultValue(
                def.defaultList.bindTarget,
                tableType,
                def.defaultList.bindExpress
              );
              const defValue = item.datas.some((sta) => {
                return sta[def.prop] === '';
              });
              if (defValue === true) {
                item.datas.forEach((detail) => {
                  detail[def.prop] = staticDef;
                });
              }
            }
          } else if (def.defaultList.bindType === 'variable') {
            new Promise((resolve) => {
              if (!def.defaultList.resultValue) {
                this.doFlowVariableQuery(def.defaultList.bindTarget).then(
                  (data: any) => {
                    if (data) {
                      def.defaultList.resultValue = Array.isArray(data)
                        ? data.join(',')
                        : data;
                    }
                    resolve();
                  }
                );
              } else {
                resolve();
              }
            }).then(() => {
              const variableDef = this.computDefaultValue(
                def.defaultList.resultValue,
                tableType,
                def.defaultList.bindExpress
              );
              const defValue = item.datas.some((sta) => {
                return sta[def.prop] === '';
              });
              if (defValue === true) {
                item.datas.forEach((detail) => {
                  detail[def.prop] = variableDef;
                });
              }
            });
          } else if (def.defaultList.bindType === 'form') {
            const linkFather = arrayFind(
              this.formConfig.formList,
              'key',
              def.defaultList.bindTarget
            );
            if (linkFather) {
              if (def.defaultList.bindField) {
                this.addFormLinkFields(linkFather, def.defaultList.bindField);
              }
              if (linkFather.value) {
                const result = this.getFormLinkValue(
                  linkFather,
                  def.defaultList
                );
                const defResult = this.computDefaultValue(
                  result,
                  tableType,
                  def.defaultList.bindExpress
                );
                const defValue = item.datas.some((sta) => {
                  return sta[def.prop] === '';
                });
                if (defValue === true) {
                  item.datas.forEach((detail) => {
                    detail[def.prop] = defResult;
                  });
                }
              }
              if (!linkFather.dvObservers) {
                linkFather.dvObservers = [];
              }
              if (!arrayFind(linkFather.dvObservers, 'key', item.key)) {
                linkFather.dvObservers.push(item);
              }
              if (!linkFather.listeners) {
                this.registerChangeListeners(linkFather);
              }
            }
          } else if (def.defaultList.bindType === 'tableSta') {
            item.datas.forEach((detail) => {
              const defValue = item.datas.some((sta: any) => {
                return sta[def.prop] === '';
              });
              if (defValue === true) {
                item.datas.forEach((detail) => {
                  detail[def.prop] = this.computDefaultValue(
                    detail[def.defaultList.bindTarget],
                    tableType,
                    def.defaultList.bindExpress
                  );
                });
              }
            });
          }
        }
      }
    });
  }

  /**
   * 
   */
  computTableColEditControl(item) {
    item.cols.forEach((colsItem, ind) => {
      // 
      if (colsItem.staticList) {
        if (colsItem.staticList.staticType === 'script') {
          const showValueConfig = colsItem.staticList.showValueConfig;
          const vars: any = [];
          showValueConfig.parameterList.forEach((pl) => {
            if (!pl.resultValue) {
              if (pl.valueType === 'variable') {
                vars.push(pl.value);
              } else if (pl.valueType === 'form') {
                // item, scriptConfig, handleType, extendObj2, linkSetItem
                pl.resultValue = this.registerFormScriptParam(
                  item,
                  showValueConfig,
                  'hiddenDatas',
                  { def: colsItem, headIndex: ind },
                  pl
                );
              }
            }
          });

          new Promise((resolve) => {
            if (vars.length > 0) {
              this.doFlowVariableQuery(vars).then((data: any) => {
                if (data) {
                  showValueConfig.parameterList.parameterList.forEach((pl) => {
                    if (pl.valueType === 'variable' && !pl.resultValue) {
                      pl['resultValue'] = data[pl.value];
                    }
                  });
                }
                resolve();
              });
            } else {
              resolve();
            }
          }).then(() => {
            this.doStaticColumnComput(
              item,
              colsItem,
              null,
              'hiddenDatas',
              null
            );
          });
        } else if (colsItem.staticList.staticType === 'tableSta') {
          item.datas.forEach((record, index) => {
            // 
            record.disable = new Array(item.cols.length);

            colsItem.staticList.staticDataList.forEach((showCols) => {
              if (
                !this.computVaild(
                  showCols.staticFilter,
                  record[showCols.staticCheck]
                    ? record[showCols.staticCheck]
                    : '',
                  showCols.staticText,
                  ''
                )
              ) {
                this.$set(record.disable, ind, 0);
              }
            });
          });
        }
      }
    });
    item.datas.forEach((record, index) => {
      // 
      record.disable = new Array(item.cols.length);
    });
  }

  computDefaultValue(result, itemType, express) {
    if (result) {
      let cr = result;
      if (itemType === 'date') {
        cr = moment(result);
        if (express.year && express.year !== 0) {
          if (express.yearSign === 1) {
            express.year = 0 - express.year;
          }
          cr = cr.add(express.year, 'years');
        }
        if (express.month && express.month !== 0) {
          if (express.monthSign === 1) {
            express.month = 0 - express.month;
          }
          cr = cr.add(express.month, 'months');
        }
        if (express.day && express.day !== 0) {
          if (express.daySign === 1) {
            express.day = 0 - express.day;
          }
          cr = cr.add(express.day, 'days');
        }
        cr = cr.format('YYYY-MM-DD');
      } else if (itemType === 'number') {
        cr = parseFloat(result);
        if (express.offset) {
          const os = parseFloat(express.offset);
          switch (express.sign) {
            case 0:
              cr += os;
              break;
            case 1:
              cr -= os;
              break;
            case 2:
              cr *= os;
              break;
            case 3:
              cr /= os;
              break;
          }
        }
      }
      return cr;
    } else {
      return '';
    }
  }

  doDatasfilter(records, key, value, type) {
    return records.filter((item) => {
      const target = item[key];
      return this.computVaild(type, target, value, '');
    });
  }

  computVaild(type, target, value, sc) {
    if (sc.relationship === '2' && sc.showType === 'variable') {
      return target.split(',').some((someItem) => {
        return this.targetFun(type, someItem, value, 'item');
      });
    } else if (sc.relationship === '1' && sc.showType === 'variable') {
      return target.split(',').every((everyItem) => {
        return this.targetFun(type, everyItem, value, 'item');
      });
    } else {
      return this.targetFun(type, target, value, 'all');
    }
  }

  targetFun(type, targetItem, value, typetDiff) {
    if (typeof targetItem === 'boolean') {
      targetItem = `${targetItem}`;
    } else if (!isNaN(targetItem)) {
      targetItem = parseFloat(targetItem);
      value = parseFloat(value);
    }
    if (type === '1' || !type) {
      if (typetDiff === 'item') {
        return targetItem === value;
      } else {
        if (
          targetItem &&
          (typeof targetItem === 'string' || typeof targetItem === 'object') &&
          targetItem.indexOf(',') !== -1
        ) {
          return targetItem.indexOf(value) > -1;
        } else {
          return targetItem == value;
        }
      }
    } else if (type === '0') {
      return targetItem > value;
    } else if (type === '2') {
      return targetItem < value;
    } else if (type === '3') {
      return targetItem !== value;
    } else if (type === '4') {
      return targetItem >= value;
    } else if (type === '5') {
      return targetItem <= value;
    } else if (type === '6') {
      if (targetItem) {
        return targetItem.indexOf(value) > -1;
      }
    } else if (type === '7') {
      if (targetItem) {
        return targetItem.indexOf(value) === -1;
      }
    }
  }

  // vaildFormShow

  // 
  datasFilter(item) {
    // 
    if (item.filterText && item.filterText.length > 0) {
      item.filterConfig = {
        cfgType: 'bind',
        filterText: item.filterText,
        strategy: 'and'
      };
      item.filterText = null;
    }

    if (item.filterConfig) {
      if (item.filterConfig.cfgType === 'bind') {
        if (
          item.filterConfig.filterText &&
          item.filterConfig.filterText.length > 0
        ) {
          Promise.all(
            item.filterConfig.filterText.map((itemList) => {
              return new Promise((resolve, reject) => {
                const param = {};
                if (itemList.textLinkValue) {
                  if (itemList.filterType === 'form') {
                    const textLinkFather = arrayFind(
                      this.formConfig.formList,
                      'key',
                      itemList.textLink
                    );
                    if (textLinkFather) {
                      param['textLinkValue'] = itemList.textLinkValue;
                      param['textLinkMethod'] = itemList.textLinkMethod;
                      param['value'] = textLinkFather.value;
                      resolve(param);

                      if (!textLinkFather.filterObservers) {
                        textLinkFather.filterObservers = [];
                      }
                      if (
                        !arrayFind(
                          textLinkFather.filterObservers,
                          'key',
                          item.key
                        )
                      ) {
                        textLinkFather.filterObservers.push(item);
                      }
                      if (!textLinkFather.listeners) {
                        this.registerChangeListeners(textLinkFather);
                      }
                    }
                  } else if (itemList.filterType === 'variable') {
                    if (
                      itemList.filterValue &&
                      itemList.filterValue.length > 0
                    ) {
                      param['textLinkValue'] = itemList.textLinkValue;
                      param['textLinkMethod'] = itemList.textLinkMethod;
                      param['value'] = itemList.filterValue;
                      resolve(param);
                    } else {
                      this.doFlowVariableQuery(itemList.textLink).then(
                        (data: any) => {
                          if (
                            data &&
                            (data instanceof String || data.length > 0)
                          ) {
                            this.$set(itemList, 'filterValue', data);
                            param['textLinkValue'] = itemList.textLinkValue;
                            param['textLinkMethod'] = itemList.textLinkMethod;
                            param['value'] = data;
                            resolve(param);
                          } else {
                            this.$set(itemList, 'filterValue', '');
                            param['textLinkValue'] = itemList.textLinkValue;
                            param['textLinkMethod'] = itemList.textLinkMethod;
                            param['value'] = '';
                            resolve(param);
                          }
                        }
                      );
                    }
                  } else if (itemList.filterType === 'static') {
                    if (itemList.textLink) {
                      param['textLinkValue'] = itemList.textLinkValue;
                      param['textLinkMethod'] = itemList.textLinkMethod;
                      param['value'] = itemList.textLink;
                      resolve(param);
                    }
                  }
                }
              });
            })
          ).then((params) => {
            const orFlag = item.filterConfig.strategy === 'or';

            const computWapper = (itemData: any) => {
              return (param: any) => {
                if (param.value && param.value instanceof Array) {
                  return this.computVaild(
                    param.textLinkMethod,
                    itemData[param.textLinkValue],
                    param.value.join(','),
                    ''
                  );
                } else {
                  return this.computVaild(
                    param.textLinkMethod,
                    itemData[param.textLinkValue],
                    param.value,
                    ''
                  );
                }
              };
            };

            if (item.type === 'table') {
              const colsDatas: Array<any> = [];
              item.oriDatas.forEach((itemDatas) => {
                const comput = computWapper(itemDatas);
                const condition = orFlag
                  ? params.some(comput)
                  : params.every(comput);
                if (condition === true) {
                  colsDatas.push(itemDatas);
                }
              });
              item.datas = colsDatas;
              this.handleTableDatasChange(item);
            } else {
              const itemListData: Array<any> = [];
              item.textListLabel.forEach((itemList: any) => {
                const comput = computWapper(itemList);
                const condition = orFlag
                  ? params.some(comput)
                  : params.every(comput);
                if (condition === true) {
                  itemListData.push({
                    value: itemList[item.dataValue],
                    label: itemList[item.dataLabel]
                  });
                }
              });
              this.$set(item, 'optionsList', itemListData);
            }
          });
        }
      } else {
        // 
        item.datas = item.oriDatas;
        // 
        if (!item.filterConfig.pushDatas) {
          item.filterConfig.parameterList.push({
            label: 'beFilterDatas',
            valueType: 'fixed',
            value: item.type === 'table' ? item.oriDatas : item.textListLabel
          });
          item.filterConfig.pushDatas = true;
        }

        this.scriptComputHandle(item, item.filterConfig, 'datasfilter');
      }
    } else {
      if (item.type === 'table') {
        item.datas = item.oriDatas;
        this.handleTableDatasChange(item);
      }
    }
  }

  recordsHanle(records, item, cols) {
    records.forEach((record, index) => {
      cols.forEach((colsItem) => {
        const key = colsItem.fieldCode ? colsItem.fieldCode : colsItem.prop;
        // 
        if (!record[key]) {
          if (colsItem.value) {
            record[key] = colsItem.value;
          } else {
            record[key] = '';
          }
        }
      });
    });
    item.datas = records;
  }

  // 
  addFlowVariablesLink(tableAddList, addViewDataLists, item, records) {
    if (tableAddList.addDataSetVal) {
      this.doFlowVariableQuery(tableAddList.addDataSetVal).then((data: any) => {
        if (data && data.length > 0) {
          let oris = data;
          // 
          if (
            !item.cols.some((col) => col.prop && col.prop.indexOf('.') > -1)
          ) {
            oris = this.washTableFlowVariable(data);
          }
          this.mergeExtendColsView(oris, addViewDataLists, tableAddList, item);
        }
      });
    }
  }

  // 
  mergeExtendColsView(datas, addViewDataLists, tableAddList, item) {
    // 
    if (!tableAddList.datas) {
      tableAddList.datas = datas;
    }

    const linkDatas = datas;
    if (linkDatas) {
      const afterDatas = cloneDeep(item.oriDatas);

      afterDatas.forEach((row) => {
        let theOne: any = null;
        if (addViewDataLists.length > 1) {
          for (let i = 0; i < linkDatas.length; i++) {
            const opt = linkDatas[i];
            if (
              addViewDataLists.every(
                (av) => opt[av.addViewData] === row[av.addDataLink]
              )
            ) {
              theOne = opt;
              break;
            }
          }
        } else {
          theOne = arrayFind(
            linkDatas,
            addViewDataLists[0].addViewData,
            row[addViewDataLists[0].addDataLink]
          );
        }
        if (theOne) {
          tableAddList.addDataValue.forEach((col) => {
            /**
             * 
             * 
             */
            if (!row[col]) {
              row[col] = theOne[col];
            }
          });
        }
      });
      item.oriDatas = afterDatas;
      this.datasFilter(item);
    }
    // else {
    //   // 
    //   const edit = tableAddList.addDataValue.some((prop) => {
    //     const col = arrayFind(item.cols, 'prop', prop);
    //     return col && col.editable === 1;
    //   });

    //   if (edit) {
    //     const tds = item.oriDatas ? item.oriDatas : item.datas;
    //     tds.forEach((data) => {
    //       tableAddList.addDataValue.forEach((col) => {
    //         if (!data[col]) {
    //           data[col] = '';
    //         }
    //       });
    //     });
    //   }
    // }
  }

  computeCellClass({ row, columnIndex, column }) {
    if (
      row.beModified &&
      Array.from(row.beModified).indexOf(column.property) > -1
    ) {
      return 'red-alert';
    }
  }

  fileType(fileName) {
    if (fileName) {
      const startIndex = fileName.lastIndexOf('.');
      const endIndex = fileName.length;
      const fileTypeList = fileName.substring(startIndex + 1, endIndex);
      if (fileTypeList === 'xlsx' || fileTypeList === 'xls') {
        return 'excel';
      } else if (fileTypeList === 'doc' || fileTypeList === 'docx') {
        return 'doc';
      } else if (fileTypeList === 'html') {
        return 'html';
      } else if (fileTypeList === 'pdf') {
        return 'pdf';
      } else if (fileTypeList === 'txt') {
        return 'txt';
      } else if (
        fileTypeList === 'jpg' ||
        fileTypeList === 'jpeg' ||
        fileTypeList === 'svg' ||
        fileTypeList === 'png' ||
        fileTypeList === 'bmp' ||
        fileTypeList === 'gif'
      ) {
        return 'tupian';
      } else {
        return 'wenjian';
      }
    }
  }

  download(path) {
    attachmentDownload(path);
  }

  showHistoryCompare(formItem) {
    this.modifysVisible = true;
    this.queryItemImprint(formItem);
  }

  queryItemImprint(formItem) {
    this.historyLoading = true;
    queryEditMarks(this.processInstanceId, formItem['key']).then(
      ({ error, data }) => {
        this.historyLoading = false;
        if (!error && data.length > 0) {
          if (formItem['type'] === 'table') {
            if (!formItem['tableCompareContainer']) {
              this.compareTableDatas(formItem, data);
            } else {
              this.tableCompareContainer = formItem['tableCompareContainer'];
              this.tableCompareColumns = formItem['cols'].filter((col) => {
                return col.label !== '' && !col.hidden;
              });
            }
          } else {
            this.tableCompareColumns = this.commonHistoryColumns;
            this.tableCompareContainer = data;
          }
        }
      }
    );
  }

  compareTableDatas(tableItem, imprint) {
    const tableCompareContainer: Array<any> = [];
    imprint.reverse();
    imprint.forEach((editHistorys) => {
      Object.keys(editHistorys.value).forEach((editMarkIndex) => {
        const row = arrayFind(tableItem.datas, 'editMarkIndex', editMarkIndex);
        if (row) {
          let beModified: any = null;
          let hr: any = null;
          if (row.beModified) {
            beModified = cloneDeep(row.beModified);
            hr = arrayFind(
              tableCompareContainer,
              'editMarkIndex',
              editMarkIndex
            );
          } else {
            beModified = new Set();
            hr = cloneDeep(row);
            tableCompareContainer.push(hr);
          }

          const eh = editHistorys['value'][editMarkIndex];
          eh.forEach((edit: any) => {
            if (edit.value) {
              beModified.add(edit['prop']);

              // if (!hr[edit['prop']]) { // hr[edit['prop']]
              if (edit.preValue) {
                hr[edit['prop']] = edit.preValue;
              } else {
                hr[edit['prop']] = '';
              }
              // }
              hr[edit['prop']] += ` -> ${edit.value}`;
              // 
              if (tableItem.refreshLoading) {
                row[edit['prop']] = edit.value;
              }
            }
          });
          this.$set(row, 'beModified', beModified);
          hr.beModified = beModified;
        }
      });
    });

    if (tableCompareContainer.length > 0) {
      tableItem.tableCompareContainer = tableCompareContainer;
      this.tableCompareContainer = tableItem['tableCompareContainer'];
      this.tableCompareColumns = tableItem['cols'].filter((col) => {
        return col.label !== '' && !col.hidden;
      });
    }
  }

  uploadTable(item) {
    this.uploadTableKey = item.key;
    this.importFiles = [];
    this.importDialogVisible = !this.importDialogVisible;
  }

  doUpload() {
    if (this.importFiles.length === 0) {
      this.$message.error('');
    } else {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.loading = true;
          const uploadForm = new FormData();
          this.importFiles.forEach((item) =>
            uploadForm.append('file', item.raw)
          );
          tableUpload(uploadForm).then(({ data, error }) => {
            if (!error) {
              this.loading = false;
              this.importDialogVisible = !this.importDialogVisible;
              this.$message.success('');
              // 
              if (this.editMarks[this.uploadTableKey]) {
                this.$delete(this.editMarks, this.uploadTableKey);
              }
              // 
              const targeTableItem = arrayFind(
                this.formConfig.formList,
                'key',
                this.uploadTableKey
              );
              const targetCols = cloneDeep(targeTableItem.cols);
              const tableDataArr: any = [];
              data.forEach((item) => {
                const obj = {};
                for (const itemName in item) {
                  if (Object.prototype.hasOwnProperty.call(item, itemName)) {
                    const colProp = arrayFind(
                      targetCols,
                      'fieldName',
                      itemName
                    );
                    if (colProp) {
                      this.$set(obj, colProp.prop, item[itemName]);
                    }
                  }
                }
                tableDataArr.push(obj);
              });
              // this.$set(targeTableItem, 'datas', tableDataArr);
              this.loadTableData(targeTableItem, tableDataArr);
            } else {
              this.loading = false;
              this.$message.error(`${error.message}`);
            }
          });
        })
        .catch(() => {});
    }
  }

  closeDialog() {
    const nu: any = this.$refs.netUpload;
    nu.clearFiles();
    this.importDialogVisible = !this.importDialogVisible;
    this.loading = false;
  }

  // 
  handleUploadFileChange(file, fileList) {
    this.importFiles = fileList;
  }

  // 
  onRemove(file, fileList) {
    this.importFiles = fileList;
  }

  // 
  exportTable(item) {
    const params = {
      headers: item.cols.filter((col) => !col.hidden),
      rows: item.datas
    };
    tableDownload(params, `${item.label}.xls`);
  }

  // 
  vaildFormShow(sc, linkFather, value) {
    let show = 'show';
    if (sc.bindField) {
      // const result = this.getFormLinkValue(linkFather, sc);
      if (
        !this.computVaild(
          sc.symbolShow,
          this.getFormLinkValue(linkFather, sc),
          sc.inputValue,
          sc
        )
      ) {
        show = 'none';
      }
    } else {
      if (!this.computVaild(sc.symbolShow, value, sc.inputValue, sc)) {
        show = 'none';
      }
    }
    return show;
  }
  // 
  addChange(val) {
    this.labelTable = val.key;
    if (
      val.addTemplate &&
      val.addTemplate.formList &&
      val.addTemplate.formList.length > 0
    ) {
      this.childFromExist = true;
      this.formAttributeAlert = cloneDeep(val.addTemplate);
      this.formAttributeAlert.formList.forEach((item) => {
        this.$set(item, 'showForm', 'show');
        this.$set(item, 'editable', 1);
        if (item.type === 'checkboxGroup') {
          this.$set(item, 'value', []);
        } else if (item.type === 'transfer') {
          this.$set(item, 'value', []);
        } else {
          this.$set(item, 'value', '');
        }
      });
    } else {
      this.childFromExist = false;
      const colsDiff: Array<any> = cloneDeep(val.cols);
      this.addTableList = colsDiff.filter(
        (col) => col.label !== '' && !col.hidden
      );
    }
    this.dialogSwitch = true;
    const colsDiff: Array<any> = cloneDeep(val.cols);
    this.addTableList = colsDiff.filter(
      (col) => col.label !== '' && col.type !== 'index' && !col.hidden
    );
  }

  treeDataTest() {
    let same: any = {};
    same = this.$refs.advance['spread'].model;
    const item = arrayFind(this.formAttribute.formList, 'key', this.labelTable);
    if (item) {
      this.doTableRowAdd(item, [same]);
    }
    this.dialogSwitch = false;
    this.cancelSwitch();
  }

  doTableRowAdd(item, records) {
    records.forEach((record) => {
      // editMarkIndex
      let key = `r${item.oriDatas ? item.oriDatas.length : item.datas.length}`;
      if (item.refreshLoading && item.loadingPrimKey.length > 0) {
        key = item.loadingPrimKey.map((key) => record[key]).join('_');
      }
      this.$set(record, 'editMarkIndex', key);
      if (item.oriDatas) {
        item.oriDatas.push(record);
      } else {
        item.datas.push(record);
      }
    });

    // 
    this.loadTableData(item, item.oriDatas ? item.oriDatas : item.datas);
  }

  // 
  handleFileRemove(file: any, ind: number) {
    const fileList = this.$refs.upload[0].uploadFiles;
    const index = fileList.findIndex((fileItem: any) => {
      return fileItem.uid === file.uid;
    });
    fileList.splice(index, 1);
    const fileIndex = this.formConfig.formList[ind].value.findIndex(
      (fileItems: any) => {
        return fileItems.uid === file.uid;
      }
    );
    this.formConfig.formList[ind].value.splice(fileIndex, 1);
  }

  // 
  successList(response: any, file: any, fileList: any, index: number) {
    if (response.data && response.data.path) {
      if (!this.formConfig.formList[index].value) {
        this.formConfig.formList[index].value = [];
      }
      this.formConfig.formList[index].value.push({
        name: file.name,
        url: file.response['data']['path'],
        uid: file.uid
      });
    } else {
      this.$message.warning(':' + response.message);
    }
  }
  // 
  buttonChange(item) {
    let linkPromise = [];
    let linkDef: any = {};
    if (item.setLink) {
      linkDef = item.setLink;
      linkPromise = item.setLink.associatedLink;
    } else if (item.defaultValue) {
      linkDef = item.defaultValue;
      linkPromise = item.defaultValue.associatedLink;
    }

    if (linkDef.scriptLinkConfig && linkDef.scriptLinkConfig.valueShow) {
      // 
      if (!linkDef.scriptLinkConfig.reqBody) {
        this.scriptComputHandle(
          item,
          linkDef.scriptLinkConfig,
          'ordinaryButtonParams',
          null,
          { linkPromise, linkDef }
        );
        return;
      }
    }
    this.fetchOrdinaryBtnService(linkPromise, item, linkDef);
  }

  fetchOrdinaryBtnService(linkPromise, item, linkDef) {
    if (linkPromise.length > 0) {
      Promise.all(
        linkPromise.map((itemItem: any) => {
          return new Promise((resove, reject) => {
            const param = { code: itemItem.code, type: itemItem.type };
            if (itemItem.valueType) {
              if (itemItem.valueType === 'fixed') {
                // 
                param['value'] = itemItem.value;
                if (param['value']) {
                  resove(param);
                } else {
                  reject();
                }
              } else if (itemItem.valueType === 'form') {
                // 
                const linkFather = arrayFind(
                  this.formConfig.formList,
                  'key',
                  itemItem.value
                );
                if (linkFather) {
                  if (!linkFather.internalObservers) {
                    linkFather.internalObservers = [];
                  }
                  if (
                    !arrayFind(linkFather.internalObservers, 'key', item.key)
                  ) {
                    linkFather.internalObservers.push(item);
                  }
                  if (!linkFather.listeners) {
                    this.registerChangeListeners(linkFather);
                  }
                  param['value'] = linkFather.value;
                  if (param['value'] && param['value'].length > 0) {
                    resove(param);
                  } else {
                    reject();
                  }
                }
              } else if (itemItem.valueType === 'variable') {
                // 
                if (itemItem.resultValue) {
                  param['value'] = itemItem.resultValue;
                  resove(param);
                } else {
                  this.doFlowVariableQuery(itemItem.value).then((data: any) => {
                    if (data) {
                      itemItem.resultValue = Array.isArray(data)
                        ? data.join(',')
                        : data;
                      param['value'] = itemItem.resultValue;
                    } else {
                      itemItem.resultValue = '';
                      param['value'] = '';
                      resove(param);
                    }
                    if (param['value']) {
                      resove(param);
                    } else {
                      reject();
                    }
                  });
                }
              } else {
                // 
                resove(param);
              }
            } else {
              // 
              resove(param);
            }
          });
        })
      ).then((paramsValues) => {
        this.doOrdinaryButtonQuery(
          item,
          paramsValues.filter((pa: any) => pa['value']),
          linkDef.scriptLinkConfig.reqBody
        );
      });
    } else {
      if (linkDef && linkDef.scriptLinkConfig) {
        this.doOrdinaryButtonQuery(item, [], linkDef.scriptLinkConfig.reqBody);
      }
    }
  }

  doOrdinaryButtonQuery(item, paramsValues, reqBody) {
    let resultList = [];
    const params: any = {};
    if (reqBody) {
      params.reqBody = JSON.stringify(reqBody);
    }
    if (item.setLink) {
      this.$set(params, 'viewId', item.setLink.linkedSerVal);
      this.$set(params, 'queryParams', paramsValues);
      this.$set(params, 'authStationAndUsers', [
        {
          stationId: this.taskDealStationId,
          userCode: this.userCode
        }
      ]);
      let fetch = dataViewQueryWithAuth;
      if (item.type === 'button') {
        fetch = fetchPushService;
      }
      fetch(params).then(({ data, error }) => {
        if (data) {
          if (item.setLink.targetObjectBut.setWay === 'bind') {
            resultList = data.rows.map((reItem) => {
              return reItem[item.setLink.targetObjectBut.bindingTargets];
            });
            const linkFather = arrayFind(
              this.formConfig.formList,
              'key',
              item.setLink.targetObjectBut.targetCol
            );
            this.formConfig.formList.forEach((fromItem) => {
              if (fromItem.key === linkFather.key) {
                if (item.setLink.targetObjectBut.targetTableCol) {
                  fromItem.datas.forEach((ele) => {
                    ele[
                      item.setLink.targetObjectBut.targetTableCol
                    ] = resultList.join(',');
                  });
                } else {
                  fromItem.value = resultList.join(',');
                }
              }
            });
          } else if (item.setLink.targetObjectBut.setWay === 'script') {
            item.setLink.targetObjectBut.scriptTargets.returnData = data.rows;
            item.setLink.targetObjectBut.scriptTargets.targetCol =
              item.setLink.targetObjectBut.targetCol;
            this.scriptComputHandle(
              item,
              item.setLink.targetObjectBut.scriptTargets,
              'ordinaryButtonValue',
              null,
              null
            );
          }
        } else {
          this.$message.error(error.message);
        }
      });
    } else {
      const def = item.defaultValue;
      this.$set(params, 'viewId', item.defaultValue.bindTarget);
      this.$set(
        params,
        'queryParams',
        paramsValues.filter((pa: any) => pa['value'])
      );
      this.$set(params, 'authStationAndUsers', [
        {
          stationId: this.taskDealStationId,
          userCode: this.userCode
        }
      ]);
      dataViewQueryWithAuth(params).then(({ data, error }) => {
        if (item.type === 'childFrom') {
          item.value.formList.forEach((list) => {
            def.paramDefList.forEach((param) => {
              if (list.key === param.key) {
                resultList = data.rows.map((reItem) => {
                  return reItem[param.value];
                });
                list.value = resultList.join(',');
              }
            });
          });
        } else {
          if (data) {
            if (def.targetObjectBut.setWay === 'bind') {
              resultList = data.rows.map((reItem) => {
                return reItem[def.targetObjectBut.bindingTargets];
              });
              this.$set(item, 'value', resultList.join(','));
              item.value = resultList.join(',');
            } else {
              def.targetObjectBut.scriptTargets.returnData = data.rows;
              def.targetObjectBut.scriptTargets.targetCol =
                def.targetObjectBut.targetCol;
              this.scriptComputHandle(
                item,
                def.targetObjectBut.scriptTargets,
                'defScriptValue',
                null,
                null
              );
            }
          } else {
            this.$message.error(error.message);
          }
        }
      });
    }
  }

  buttonParameter(row, btn, item) {
    // 
    if (btn.scriptLinkConfig && btn.scriptLinkConfig.valueShow) {
      // 
      if (!btn.scriptLinkConfig.reqBody) {
        this.scriptComputHandle(
          item,
          btn.scriptLinkConfig,
          'paramsButtonValue',
          null,
          { row, btn }
        );
        return;
      }
    }
    this.fetchBtnService(row, btn, item);
  }

  fetchBtnService(row, btn, item) {
    const linkPromise = btn.associatedLink;
    if (linkPromise && linkPromise.length > 0) {
      // 
      Promise.all(
        linkPromise.map((itemItem) => {
          return new Promise((resove) => {
            const param = { code: itemItem.code, type: itemItem.type };
            if (itemItem.valueType) {
              if (itemItem.valueType === 'fixed') {
                // 
                param['value'] = itemItem.value;
                resove(param);
              } else if (itemItem.valueType === 'form') {
                // 
                const linkFather = arrayFind(
                  this.formConfig.formList,
                  'key',
                  itemItem.value
                );
                param['value'] = linkFather.value;
                resove(param);
              } else if (itemItem.valueType === 'variable') {
                // 
                if (itemItem.resultValue) {
                  param['value'] = itemItem.resultValue;
                  resove(param);
                } else {
                  this.doFlowVariableQuery(itemItem.value).then((data: any) => {
                    if (data) {
                      itemItem.resultValue = Array.isArray(data)
                        ? data.join(',')
                        : data;
                      param['value'] = itemItem.resultValue;
                      resove(param);
                    } else {
                      // 
                      itemItem.resultValue = '';
                      param['value'] = '';
                      resove(param);
                    }
                  });
                }
              } else if (itemItem.valueType === 'tableSta') {
                // 
                param['value'] = row[itemItem.value];
                resove(param);
              }
            }
          });
        })
      ).then((paramsValues) => {
        this.doTableCellBtn(btn, paramsValues, btn.targetObjectBut, row, item);
      });
    } else {
      this.doTableCellBtn(btn, [], btn.targetObjectBut, row, item);
    }
  }

  doTableCellBtn(btn, queryParams, targetObjectBut, row, item) {
    let resultList = [];
    const params: any = {
      viewId: btn.linkedSerVal,
      queryParams
    };
    if (btn.scriptLinkConfig.reqBody) {
      params.reqBody = JSON.stringify(btn.scriptLinkConfig.reqBody);
    }
    fetchPushService(params).then(({ data, error }) => {
      if (data) {
        if (targetObjectBut.setWay === 'bind') {
          resultList = data.rows.map((reItem) => {
            return reItem[targetObjectBut.bindingTargets];
          });
          this.$set(row, targetObjectBut.targetCol, resultList.join(','));
        } else if (targetObjectBut.setWay === 'script') {
          targetObjectBut.scriptTargets.returnData = data.rows;
          targetObjectBut.scriptTargets.targetCol = targetObjectBut.targetCol;
          this.scriptComputHandle(
            item,
            targetObjectBut.scriptTargets,
            'resultButtonValue',
            null,
            row
          );
        }
      } else {
        this.$message.error(error.message);
      }
    });
  }
  // 
  changeClickBut(row, item, itemTable) {
    if (item.setLink) {
      this.buttonParameter(row, item.setLink, itemTable);
    }
  }

  loadingColumns(cols, item) {
    return cols.filter((col) => {
      const { tableFormConfig } = col;
      if (col.editable) {
        const type = tableFormConfig ? tableFormConfig.formType : 'input';
        if (
          type === 'select' ||
          type === 'checkboxGroup' ||
          type === 'radioGroup'
        ) {
          this.$set(tableFormConfig, 'optionsList', []);
          this.$set(tableFormConfig, 'optionsType', '');
          if (tableFormConfig.sourceDataType === 'dataView') {
            // 
            setTimeout(() => {
              if (tableFormConfig.dataQueryList.length > 0) {
                this.listQueryHandle(
                  tableFormConfig,
                  tableFormConfig.dataQueryList,
                  tableFormConfig.dataSetVal,
                  'selectView'
                );
              } else {
                this.dataViewQueryHandle(
                  tableFormConfig.dataSetVal,
                  tableFormConfig,
                  'selectView'
                );
              }
            }, 500);
          } else if (tableFormConfig.sourceDataType === 'variable') {
            if (tableFormConfig.flowVisibleCode) {
              this.getFlowVariablehttp('variableChange', tableFormConfig);
            }
          } else if (tableFormConfig.sourceDataType === 'static') {
            // 
            if (tableFormConfig.value) {
              setTimeout(() => {
                if (
                  tableFormConfig.listeners &&
                  tableFormConfig.listeners.change
                ) {
                  tableFormConfig.listeners.change(
                    tableFormConfig.value,
                    'auto'
                  );
                }
              }, 500);
            }
          }
        }
        col.editer = {
          key: col.prop,
          type: col.tableFormConfig ? col.tableFormConfig.formType : 'input',
          config: col.tableFormConfig
        };
      }
      if (!col.hidden && col.format) {
        this.configFormatCol(col);
      }
      if (col.label === '') {
        col.actions = [];
        if (col.buttonAdd) {
          col.actions = col.buttonAdd.map((btn) => {
            return {
              code: btn.fieldCode,
              type: btn.fieldType,
              text: btn.fieldName,
              onClick: (row) => this.changeClickBut(row, btn, item)
            };
          });
        }
        if (item.editable === 1 && item.addLine) {
          col.actions.push({
            code: 'deleteRequired',
            text: '',
            onClick: this.deleteEdit
          });
        }
      }
      return !col.hidden;
    });
  }

  configFormatCol(col) {
    if (!col.formatter) {
      switch (col.format) {
        case 'thousand':
          col.formatter = (row, column, cellValue) => {
            return currencyFormatter(cellValue, col.accuracy);
          };
          break;
        case 'thousandPercent':
          col.formatter = (row, column, cellValue) => {
            return currencyFormatter(cellValue * 100, col.accuracy) + '%';
          };
      }
    }
  }
}
