




































































































































































































































import { Component } from 'vue-property-decorator';
import previewFormChild from './previewFormChild.vue';
import addContent from '../../../viewDateManagent/component/addContent.vue';
import customItemRender from '@components/renders/customItemRender.js';
import ueditor from './ueditor.vue';
import formMixin from './mixin';
@Component({
  components: { customItemRender, ueditor, addContent, previewFormChild }
})
export default class PreviewFormChild extends formMixin {
  // -
  cancelSwitch() {
    this.addTableList = [];
    this.dialogSwitch = false;
  }
  created() {
    if (this.formAttribute) {
      this.getVisible(this.formAttribute);
    }
    this.userCode = this.$store.state.user.userInfo.usercode;
  }
}
