

























import { Vue, Component, Prop, Emit, Watch } from 'vue-property-decorator';
import AdvancedScreening from './advanced.vue';
@Component({
  components: { AdvancedScreening }
})
export default class AddContent extends Vue {
  spread: any = {
    visible: true,
    model: {},
    items: [],
    loading: false
  };
  @Prop(Array) paramsList: [];
  @Prop(String) dialogTitle: '';
  @Prop({ default: false }) dialogTestSwitch: boolean;
  @Emit('saveEmitSwitch')
  treeDataTest() {}
  @Emit('cancelEmitSwitch')
  cancelSwitch() {}
  @Watch('paramsList', { immediate: true, deep: true })
  getVisible(newVal: any) {
    const kong: Array<any> = [];
    const dataModel: any = {};
    newVal.forEach((item: any) => {
      if (item.value) {
        dataModel[item.fieldCode] = item.value;
      } else if (item.dafalutValue) {
        dataModel[item.fieldCode] = item.dafalutValue;
      } else {
        dataModel[item.fieldCode] = '';
      }
      if (item.fieldType === 3) {
        kong.push({
          key: item.fieldCode,
          label: item.fieldName,
          type: 'switch'
        });
      } else if (item.fieldType === 2) {
        kong.push({
          key: item.fieldCode,
          label: item.fieldName,
          type: 'date',
          TimePickerOptions: '',
          clearable: true,
          disabled: false
        });
      } else {
        kong.push({
          type: 'input',
          key: item.fieldCode,
          label: item.fieldName,
          placeholder: item.fieldType === 1 ? '' : '',
          config: {
            props: {
              type: item.fieldType === 1 ? 'number' : 'input'
            }
          }
        });
      }
    });
    this.spread.items = kong;
    this.spread.model = dataModel;
    // console.log("this.spread", this.spread);
  }
}
