<template>
  <div class="edit_container" style="height:158px;">
    <quill-editor
      ref="myQuillEditor"
      v-model="itemValue.value"
      class="quillEditor"
      :options="editorOption"
      :disabled="disable"
      @blur="onEditBlur(itemValue.value)"
      @focus="onEditorFocus($event)"
      @ready="onEditorReady($event)"
      @change="alertValue($event, 400, decimalNum)"
    >
    </quill-editor>
  </div>
</template>

<script>
import { quillEditor } from 'vue-quill-editor';
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
export default {
  name: 'QuillEditor',
  components: {
    quillEditor
  },
  data() {
    return {
      content: `<p>hello world</p>`,
      editorOption: {
        modules: {
          toolbar: [
            ['bold', 'italic', 'underline', 'strike'], // 
            ['blockquote', 'code-block'], // 
            [{ header: 1 }, { header: 2 }], // 12
            [{ list: 'ordered' }, { list: 'bullet' }][ // 
              ({ script: 'sub' }, { script: 'super' })
            ], // 
            [{ indent: '-1' }, { indent: '+1' }], // 
            [{ direction: 'rtl' }][{ size: ['small', false, 'large', 'huge'] }] //  // 
            // [{ header: [1, 2, 3, 4, 5, 6, false] }][ // 
            // ({ color: [] }, { background: [] })
            // ], // 
            // [{ font: [] }], // 
            // [{ align: [] }], // 
            // ['clean'], // 
            // ['image', 'video'] // 
          ]
        },
        theme: 'snow'
      }
    };
  },
  props: {
    onEditBlur: {
      type: Function,
      default: () => ''
    },
    itemValue: {
      type: Object,
      default: () => {}
    },
    disable: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    editor() {
      return this.$refs.myQuillEditor.quill;
    }
  },
  methods: {
    onEditorBlur(itemValue) {
      console.log(itemValue, 'itemValue');
      this.$emit('onEditorBlur', itemValue);
    }, // 
    onEditorFocus() {}, // 
    onEditorReady() {},
    saveHtml: function(event) {
      alert(this.itemValue);
    },
    alertValue(e, strValue, decimalNum) {}
  }
};
</script>
