


























































































































































































































































import { Component } from 'vue-property-decorator';
import { arrayFind } from '@utils/util';
import previewFormChild from './previewFormChild.vue';
import addContent from '../../../viewDateManagent/component/addContent.vue';
import customItemRender from '@components/renders/customItemRender.js';
import ueditor from './ueditor.vue';
import formMixin from './mixin';
import { cloneDeep } from 'lodash';
@Component({
  components: { customItemRender, ueditor, addContent, previewFormChild }
})
export default class PreviewForm extends formMixin {
  // 
  deleteEdit(row) {
    this.formAttribute.formList.forEach((item) => {
      if (item.type === 'table') {
        for (let i = 0; i < item.datas.length; i++) {
          if (item.datas[i].editMarkIndex === row.editMarkIndex) {
            item.datas.splice(i, 1);
          }
        }
      }
    });
  }
  tableFormSave(form) {
    const same: any = {};
    const item = arrayFind(this.formAttribute.formList, 'key', this.labelTable);
    form.formList.forEach((ele) => {
      item.cols.forEach((e) => {
        // debugger
        if ((ele.fieldName || ele.label) === e.fieldName) {
          if (ele.value instanceof Array && ele.value !== '') {
            // console.log('2',ele.value)
            this.$set(same, e.fieldCode, ele.value.join(','));
          } else {
            // console.log('3',ele.value)
            this.$set(
              same,
              e.fieldCode,
              ele.realValue ? ele.realValue : ele.value
            );
          }
        }
      });
    });

    let splitTable = item.splitTable;
    if (splitTable) {
      if (!Array.isArray(splitTable)) {
        splitTable = [splitTable];
      }
      const batRows = splitTable.reduce(
        (prev, cur) => {
          const tr: any = [];
          prev.forEach((entity) => {
            same[cur].split(',').forEach((bat) => {
              const ssame = cloneDeep(entity);
              ssame[cur] = bat;
              tr.push(ssame);
            });
          });
          return tr;
        },
        [same]
      );

      this.doTableRowAdd(item, batRows);
    } else {
      this.doTableRowAdd(item, [same]);
    }
    this.dialogSwitch = false;
  }
  // -
  cancelSwitch() {
    this.addTableList = [];
    this.dialogSwitch = false;
  }

  created() {
    if (this.formAttribute) {
      this.getVisible(this.formAttribute);
    }
    this.userCode = this.$store.state.user.userInfo.usercode;
  }
}
