

























































































import { Component, Vue } from 'vue-property-decorator';
import {
  getPageTemplateList,
  deletePageTemplate,
  releasePageTemplate
} from '@api/pageTemplate';
import { addMenu } from '@api/authority';
import PreviewTemplate from './components/previewTemplate.vue';
import { cloneDeep } from 'lodash';
import { randomString } from './utils/util';
import paramsCache from '@mixins/paramCache';

Component.registerHooks(['beforeRouteLeave']);
const paramsCacheMixin = paramsCache([
  'currentPage',
  'pageSize',
  'total',
  'templateList',
  'templateName'
]);
@Component({
  components: { PreviewTemplate },
  mixins: [paramsCacheMixin]
})
export default class PageTemplate extends Vue {
  private templateList: Array<any> = [];
  private templateName = '';
  private loading = false;
  private currentPage = 1;
  private pageSize = 10;
  private total = 100;
  private templateId: any = '';
  private dialogVisible = false;
  private previewDialogVisible = false;
  private defaultProps: any = {
    children: 'children',
    label: 'title'
  };
  private selectedMenu: any = {
    menuTitle: '',
    id: '',
    code: ''
  };
  private currentTemplate: any = null;
  private showError = false;
  private menuRules: any = {
    menuTitle: [{ message: '', trigger: 'blur', required: true }]
  };

  get menuData() {
    const authorizes = cloneDeep(this.$store.getters.authorizes);
    const result = this.formatMenuData(authorizes);
    return result;
  }

  // 
  formatMenuData(data: Array<any>) {
    const result: Array<any> = [];
    data.forEach((item: any) => {
      if (item.children && item.children.length) {
        item.children = this.formatMenuData(item.children);
        result.push(item);
      }
    });
    return result;
  }

  // 
  columns() {
    const _this = this;
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '',
        width: '60'
      },
      {
        prop: 'name',
        label: '',
        'header-align': 'left',
        showOverflowTooltip: true
      },
      {
        prop: 'status',
        label: '',
        align: 'center',
        width: '90',
        render(h: any, { record }: any) {
          let statusText = '';
          if (record.status === 0) {
            statusText = '';
          } else if (record.status === 1) {
            statusText = '';
          } else {
            statusText = '';
          }
          return h('span', {}, statusText);
        }
      },
      {
        prop: 'modifier',
        label: '',
        align: 'center',
        width: '90'
      },
      {
        prop: 'editTime',
        label: '',
        align: 'center',
        width: '170'
      },
      {
        prop: '',
        align: 'center',
        label: '',
        width: '300',
        actions: [
          {
            code: 'preview',
            text: '',
            onClick: _this.preview
          },
          {
            code: 'release',
            text: (record: any) => (record.status === 1 ? '' : ''),
            onClick: _this.release
            // disabled: () => true
          },
          {
            code: 'edit',
            text: '',
            onClick: _this.modify
          },
          {
            code: 'copy',
            text: '',
            onClick: _this.copy
          },
          {
            code: 'delete',
            text: '',
            onClick: _this.delete
          }
        ]
      }
    ];
  }

  // 
  titleBtns() {
    const _this = this;
    return [
      {
        title: '',
        icon: 'add-line',
        onClick: _this.addPageTemplate
      }
    ];
  }

  // pageSize
  handleSize(val: any) {
    this.pageSize = val;
    this.currentPage = 1;
    this.getTemplateList();
  }

  // 
  handleCurrent(val: any) {
    this.currentPage = val;
    this.getTemplateList();
  }

  // 
  selectList() {
    this.currentPage = 1;
    this.getTemplateList();
  }

  // 
  getTemplateList() {
    const param: any = {
      page: this.currentPage,
      pageSize: this.pageSize
    };
    if (this.templateName) {
      param.name = this.templateName;
    }
    this.loading = true;
    getPageTemplateList(param).then(({ data, error }: any) => {
      this.loading = false;
      if (!error) {
        this.templateList = data.rows;
        this.total = data.total;
      } else {
        this.$message.error(error.message || '');
      }
    });
  }

  // 
  preview(pageTemplate: any) {
    this.templateId = pageTemplate.id;
    this.previewDialogVisible = true;
    this.$nextTick(() => {
      (this.$refs.previewTemplate as any).initPreviewTemplate();
    });
  }

  // 
  release(pageTemplate: any) {
    this.currentTemplate = pageTemplate;
    if (pageTemplate.status === 1 || pageTemplate.status === 2) {
      // 12 
      this.$confirm(
        pageTemplate.status === 1 ? '?' : '?',
        '',
        {
          confirmButtonText: pageTemplate.status === 1 ? '' : '',
          cancelButtonText: '',
          type: 'warning'
        }
      )
        .then(() => {
          const register = pageTemplate.status === 1 ? 0 : 1;
          this.registerMenu(register);
        })
        .catch(() => {});
    } else if (pageTemplate.status === 0) {
      //  
      this.dialogVisible = true;
      this.selectedMenu.menuTitle =
        this.currentTemplate.menuTitle || this.currentTemplate.name;
    }
  }

  // 
  registerMenu(operation) {
    if (operation === 1) {
      // 
      const parentId =
        this.currentTemplate.parentMenuId || this.selectedMenu.id;
      const parentCode =
        this.currentTemplate.parentMenuCode || this.selectedMenu.code;
      const title =
        this.currentTemplate.menuTitle || this.selectedMenu.menuTitle;
      const menuCode = 'dynamicPage' + this.currentTemplate.id;
      const param: any = {
        actions: [
          {
            code: `${parentCode}_${menuCode}:query`,
            name: ''
          }
        ],
        code: menuCode,
        path: menuCode,
        type: 0,
        href: this.currentTemplate.id,
        parent: parentId,
        title: title,
        icon: 'new-page',
        order: 0
      };
      // console.log(param);
      addMenu(param).then(({ error }) => {
        if (!error) {
          this.doRelease();
        } else {
          this.$message.error(error.message);
        }
      });
    } else {
      // TODO: 
      // addMenu(param).then(({error}) => {
      //   if(!error) {
      this.doRelease();
      // } else {
      //   this.$message.error(error.message);
      // }
      // });
    }
  }

  // 
  doRelease() {
    const id = this.currentTemplate.id;
    const currentStatus = this.currentTemplate.status;
    let parentMenuId = '';
    let parentMenuCode = '';
    let menuTitle = '';
    if (currentStatus === 0) {
      parentMenuId = this.selectedMenu.id; // 
      parentMenuCode = this.selectedMenu.code;
      menuTitle = this.selectedMenu.menuTitle;
    } else if (currentStatus === 2) {
      parentMenuId = this.currentTemplate.parentMenuId; // parentMenuId
      parentMenuCode = this.currentTemplate.parentMenuCode;
      menuTitle = this.currentTemplate.menuTitle;
    }

    const param: any = {
      id,
      status: currentStatus === 1 ? 0 : 1 // 
    };
    if (currentStatus === 0 || currentStatus === 2) {
      param.parentMenuId = parentMenuId;
      param.parentMenuCode = parentMenuCode;
      param.menuTitle = menuTitle;
    }
    // console.log('dd', param);
    releasePageTemplate(param).then(({ error }: any) => {
      if (!error) {
        this.$message.success(currentStatus === 1 ? '' : '');
        if (currentStatus === 0) {
          // 
          this.cancelDialog();
        }
        this.getTemplateList();
      } else {
        this.$message.error(error.message);
      }
    });
  }

  // 
  modify(pageTemplate: any) {
    this.$router.push({
      path: '/sysSupport/editPageTemplate',
      query: { id: pageTemplate.id, type: 'modify' }
    });
  }

  // 
  copy(pageTemplate: any) {
    this.$router.push({
      path: '/sysSupport/editPageTemplate',
      query: { id: pageTemplate.id, type: 'copy' }
    });
  }

  // 
  delete(pageTemplate: any) {
    this.$confirm('?', '', {
      confirmButtonText: '',
      cancelButtonText: '',
      type: 'warning'
    })
      .then(() => {
        const param = {
          id: pageTemplate.id
        };
        deletePageTemplate(param).then(({ error }: any) => {
          if (!error) {
            this.$message.success('');
            this.getTemplateList();
          } else {
            this.$message.error(error.message);
          }
        });
      })
      .catch(() => {});
  }

  // 
  addPageTemplate() {
    this.$router.push({
      path: '/sysSupport/editPageTemplate',
      query: { type: 'new' }
    });
  }

  // 
  saveDialog() {
    const menuForm: any = this.$refs['menuForm'];
    menuForm.validate((valid: any) => {
      if (valid) {
        if (!this.selectedMenu.id) {
          this.showError = true;
          return;
        }

        this.registerMenu(1);
      }
    });
  }

  // 
  cancelDialog() {
    this.dialogVisible = false;
    this.selectedMenu = {
      menuTitle: '',
      id: '',
      code: ''
    };
    this.currentTemplate = null;
    this.showError = false;
  }

  // 
  handleNodeClick(data: any, node: any, element) {
    // if (node.isLeaf) {
    this.selectedMenu.id = data.id;
    this.selectedMenu.code = data.code;
    this.showError = false;
    const a: any = this.$refs.menuTree;
    a.treeItems.forEach((item: any) => {
      item.removeAttribute('selected');
      item
        .getElementsByClassName('el-tree-node')
        .forEach((item: any) => item.removeAttribute('selected'));
    });
    element.$el.setAttribute('selected', true);
    // }
  }

  created() {
    !this.templateList.length && this.getTemplateList();
  }

  beforeRouteLeave(to, from, next) {
    this.cacheParamData();
    next();
  }
}
